.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  font-family: "Amazon Ember", Arial, sans-serif;
}
.login__image {
  width: 100px;
  /* display: inline; */
  /* object-fit: contain; */
  /* margin: 0 20px; */
  margin-top: 5px;
  /* text-align: center !important; */
  /* justify-content: center !important; */
  /* opacity filter: opacity(50%); }; */
  /* opacity: filter opacity(50%); */
}
.img {
  text-align: center !important;
  margin-top: 20px;
}
.in {
  text-decoration: none;
  color: black;
  width: 14px;
  text-align: center;
  height: 28px;
}

.login__container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
  font-family: "Amazon Ember", Arial, sans-serif;
}
.login__container > h1 {
  font-weight: 400;
  font-size: 28px;
  line-height: 1.2;
  margin-bottom: 10px !important;
  /* font-family: Arial, sans-serif; */
  text-rendering: optimizeLegibility;
  padding-bottom: 4px;
  padding: 0;
  margin: 0;
}
.login__container form > h5 {
  display: block;
  padding-left: 2px;
  padding-bottom: 2px;
  font-weight: 700;
  font-size: 13px;
  line-height: 19px;
  color: #111;
  /* font-family: Arial, sans-serif; */
}
.login__container form > input {
  width: 100%;
  margin-right: 0;
  background-color: #fff;
  height: 31px;
  padding: 3px 7px;
  line-height: normal;
  border: 1px solid #a6a6a6;
  border-top-color: #949494;
  border-radius: 3px;
  box-shadow: 0 1px 0 rgb(255 255 255 / 50%), 0 1px 0 rgb(0 0 0 / 7%) inset;
  outline: 0;
  color: #111;
}
