  margin-top: 10px;
  text-align: center;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  font-size: 13px;
  padding: 0 10px 0 11px;
}
.login__password {
  margin-top: 10px;
}
.textprivacy {
  font-size: 12px !important;
  line-height: 1.5 !important;
  margin-top: 18px !important;
  width: 100%;
}

.login__registerButton {
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  text-align: center;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  font-size: 13px;
  padding: 0 10px 0 11px;
