.paragraphLink {
  color: #007185;
}
.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 22px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  position: relative;
  padding-top: 10px;
  /* max-height: 420px; */
  /* height: 420px; */

  overflow: hidden;
  max-width: 1480px;
  min-width: 200px;
  background-color: white;
  z-index: 1;
}
.product > img {
  max-height: 275px;
  width: 100%;
  height: 375px;
  margin-bottom: 15px;
}
.product__info {
  /* max-height: 54.6px; */
  font-size: 21px !important;
  line-height: 27.3px !important;
  padding-bottom: 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  padding: 0;
  margin: 0;
  /* display: flex; */
  margin-right: auto;
  /* padding-bottom: 10px; */
}
.paragraphLink {
  text-decoration: none;
  margin-right: auto;
  font-size: 13px !important;
  line-height: 19px !important;
  font-family: "Amazon Ember", Arial, sans-serif;
  margin-bottom: 20px;
}
.paragraphLink1 {
  color: #007185;
  text-decoration: none;
  padding-left: 15px;
  font-size: 13px !important;
  font-family: "Amazon Ember", Arial, sans-serif;
  /* margin-top: 2px; */
}
.product > button {
  background: #f0c14b;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  border-radius: 2px;
  height: 20px;
  margin-top: -10px;
  margin-bottom: 10px;
  text-align: center;
  font-size: 13px;
  padding: 0 10px 0 11px;
}
.product__price{
  display :flex;
  margin-top: -20px;
}

